body {
  margin: 0;
  padding: 0;
}

* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-weight: 400;
}

*:focus {
outline: #000;
}

.login-container{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 200px;
  padding: 50px;
  font-family: 'Raleway', sans-serif;
}

.contact-form{
  width: 400px;
  background-color: #fff;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.1);
  padding: 50px;
  color: #808080;
}

.contact-form img{
  width: 30%;
  margin-bottom: 30px;
}

.contact-form h1{
  margin-bottom: 2px;
  color: #000;
  font-weight: 800;
}

.contact-form h4{
  margin-bottom: 50px;
}

.inputs{
  display: flex;
  flex-direction: column;
}

.email-input{
  height: 45px;
  margin-bottom: 20px;
  margin-top: 5px;
  background: transparent;
  color: #808080;
  border: 1px solid #DCDCDC;
  border-radius: 6px;
}

.email-input:focus{
  border-color: #000;
}

.checkbox{
  margin-right: 10px;
  margin-bottom: 45px;
 
}

.btn{
  width: 100px;
  height: 40px;
  background: transparent;
  color: #808080;
  border: 1px solid #DCDCDC;
  
  border-radius: 6px;

}

.btn:hover{
  cursor: pointer;
  border: 1px solid #808080;
}

.btn:focus{
  border-color: #000;
}

.btn:active{
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

@media screen and (max-width: 550px) {
  .contact-form h1{
   font-size: 30px;
  }
}